import React, { useRef, useEffect } from 'react';

import Webcam from 'react-webcam';
import domtoimage from 'dom-to-image';
import { saveAs } from 'file-saver';
import { CSSProperties } from '@material-ui/core/styles/withStyles';

import Image from '../types/Image';

interface Props {
  shotting: boolean
  setShotting: (shotting: boolean) => void
  image: Image
  windowWidth: number
}

function Kaodashi(props: Props) {
  const {
    shotting,
    setShotting,
    image,
    windowWidth,
  } = props;

  const kaodashiRef = useRef<HTMLDivElement>(null);
  const webcamRef = useRef<Webcam & HTMLVideoElement>(null);
  const canvasRef = useRef<HTMLCanvasElement>(null);

  useEffect(() => {
    if (!shotting) {
      return;
    }
    if (
      !kaodashiRef.current
      || !webcamRef.current || !webcamRef.current.video
      || !canvasRef.current
    ) {
      return;
    }
    const ctx = canvasRef.current.getContext('2d');
    ctx?.drawImage(webcamRef.current.video, 0, 0);
    domtoimage.toBlob(kaodashiRef.current)
      .then(blob => {
        saveAs(blob, 'screenshot.png');
        setShotting(false);
      })

  }, [shotting, setShotting]);

  const cameraSize = image.head_width * 2;
  const cameraSizeResized = cameraSize / image.width * windowWidth;
  const headPosition = image.heads_position[0];
  const cameraPositionStyle: CSSProperties = {
    position: 'absolute',
    top: (headPosition[1] - cameraSize / 2) / image.width * windowWidth,
    left: (headPosition[0] - cameraSize / 2) / image.width * windowWidth,
  }

  return (
    <div ref={kaodashiRef} style={{ height: image.height / image.width * windowWidth }}>
      <Webcam
        mirrored
        audio={false}
        videoConstraints={{ width: cameraSizeResized, height: cameraSizeResized }}
        ref={webcamRef}
        style={{ display: shotting ? 'none' : 'initial', ...cameraPositionStyle }}
      />
      <canvas
        ref={canvasRef}
        width={cameraSizeResized}
        height={cameraSizeResized}
        hidden={shotting ? false : true}
        style={{ transform: "scaleX(-1)", ...cameraPositionStyle }}
      />
      <img
        src={`/images/${props.image.key}.png`}
        alt="ME"
        style={{ position: 'absolute', width: '100%' }}
      />
    </div>
  )
}

export default Kaodashi;